* {
  font-family: Arial, Helvetica, sans-serif;
}
.app-wrapper {
  margin: 0 auto;
  width: 90%;
  display: grid;
  grid-template-areas: 
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 15px;
}
.content {
  grid-area: c;
  background-color: honeydew;
}



